*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font: bold 15px 'Comic Sans';
}

#main{
    width: 360px;
    height: 420;
    background-color: green;
    border: 10px solid black;
    border-radius: 15px;
    margin: 100px auto;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center
} 
    #stage{
        width: 300px;
        height: 300px;
        border: 5px solid black;
        position: relative;
    }

    #status{
        display: flex;
        flex-flow: row;
        justify-content: space-between;
    }
